import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";

import "./SubMajorEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import trainingMajorApi from "api/common/trainingMajor";
import subMajorApi from "api/common/subMajor";
import {
    FaExchangeAlt,
    FaPlus,
    FaSync,
    FaTextWidth,
    FaTrash,
    FaUserAlt,
    FaUsersCog,
} from "react-icons/fa";
import {
    Button,
    Form,
    FormGroup,
    FormText,
    Input,
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { isErrors } from "utils/utils";

const initValues = {
    khoa: new Date().getFullYear(),
    coHoiNgheNghiep: "",
    mucTieuChung: "",
    nganhDaoTao: 0,
};

const SubMajorEdit = function ({ values, handleChange, resetForm, errors }) {
    const { t } = useTranslation();

    const history = useHistory();

    const [id, setId] = useState(null);
    const [isUpdatePage, setIsUpdatePage] = useState(false);
    const [notification, setNotification] = useState({
        color: "danger",
        isOpen: false,
        messages: [],
    });
    const [isLoading, setIsLoading] = useState(false);
    const [listMajor, setListMajor] = useState([{ id: 0, ten: "" }]);

    const availableStatus = [
        {
            value: true,
            name: t("admin.common.DELETED"),
        },
        {
            value: false,
            name: t("admin.common.AVAILABLE"),
        },
    ];

    useEffect(async () => {
        const parsed = queryString.parse(window.location.search);
        if (parsed.id) {
            setId(parsed.id);
            setIsUpdatePage(true);
            updateForm(parsed.id);
        } else {
            const res2 = await trainingMajorApi.getAll();
            if (res2) {
                setListMajor(res2.contents);
            }
        }
    }, []);

    const updateForm = async function (id) {
        setIsLoading(true);
        try {
            const res = await subMajorApi.getById(id);
            if (res) {
                values.khoa = res.khoa;
                values.coHoiNgheNghiep = res.coHoiNgheNghiep;
                values.mucTieuChung = res.mucTieuChung;
                values.nganhDaoTao = res.nganhDaoTao.id;
            }
            const res2 = await trainingMajorApi.getAll();
            if (res2) {
                setListMajor(res2.contents);
            }
        } catch (err) {
            if (err.response) {
                if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
                    toast.error(t("RESPONSE_MESSAGE403"));
                  }
                else if (err?.response?.status === 401) {
                    toast.error(t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("RESPONSE_MESSAGE404"));
                } else {
                    toast.error(t("RESPONSE_MESSAGE500"));
                }
            }
        }
        setIsLoading(false);
    };

    const resetNoti = () => {
        setNotification({
            color: "success",
            isOpen: false,
            messages: [],
        });
    }
    const handleEditOnClick = async function (e) {
        e.preventDefault();
        resetNoti();
        try {
            let valuesSend = {};
            valuesSend.khoa = values.khoa;
            valuesSend.coHoiNgheNghiep = values.coHoiNgheNghiep;
            valuesSend.mucTieuChung = values.mucTieuChung;
            valuesSend.nganhDaoTao = parseInt(values.nganhDaoTao, 10);
            if (isUpdatePage) {
                const res = await subMajorApi.putById(id, valuesSend);
                if (res) {
                    setNotification({
                        color: "success",
                        isOpen: true,
                        messages: [t("admin.UPDATE_SUCCESS")],
                    });
                    updateForm(id);
                }
            } else {
                const res = await subMajorApi.post(valuesSend);
                if (res) {
                    if (res?.message === 'CREATE_CTNGANHDAOTAO_SUCCESSFULLY') {
                        setNotification({
                            color: "success",
                            isOpen: true,
                            messages: [t("admin.ADD_SUCCESS")],
                        });
                    } else if (res?.error === 'CTNGANHDAOTAO_EXIST') {
                        setNotification({
                            color: "danger",
                            isOpen: true,
                            messages: [t("CTNGANHDAOTAO_EXIST")],
                        });
                    }
                    else {
                        setNotification({
                            color: "danger",
                            isOpen: true,
                            messages: [t("ERROR")],
                        });
                    }
                }
            }
        } catch (err) {
            if (err?.response) {
                if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
                    toast.error(t("RESPONSE_MESSAGE403"));
                  }
                else if (err?.response?.status === 401) {
                    toast.error(t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("RESPONSE_MESSAGE404"));
                } else {
                    if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
                        toast.error(t("ER_DUP_ENTRY"));
                    } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                        toast.error(t("RESPONSE_MESSAGE404"));
                    } else if (String(err?.response?.data?.error)?.includes("CTNGANHDAOTAO_EXIST")) {
                        toast.error(t("RESPONSE_MESSAGE409"));
                    } else {
                        toast.error(t("RESPONSE_MESSAGE500"));
                    }
                }
            }
        }
    };

    const handleRefreshOnClick = function (e) {
        e.preventDefault();
        resetForm();
        resetNoti();
    };

    const handleDeleteOnClick = async function (e) {
        e.preventDefault();
        resetNoti();
        try {
            const res = await subMajorApi.deleteById(id);
            if (res) {
                if (String(res?.message) === 'DELETE_CTNGANHDAOTAO_SUCCESSFULLY') {
                    toast.success(t("admin.common.DELETED"));
                    history.push(RoutesString.ADMIN_SUB_MAJOR_LIST)
                } else {
                    setNotification({
                        color: "danger",
                        isOpen: true,
                        messages: [t("ERROR")],
                    });
                }
            }
        } catch (err) {
            if (err.response) {
                if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
                    toast.error(t("RESPONSE_MESSAGE403"));
                  }
                else if (err?.response?.status === 401) {
                    toast.error(t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
                } else {
                    if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                        toast.error(t("RESPONSE_MESSAGE404"));
                    } else {
                        toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
                    }
                }
            }
        }
    };

    return (
        <div className="page">
            {isLoading && <LoadingBar />}

            <Breadcrumbs
                currentItem={
                    isUpdatePage
                        ? t("admin.sub_major_edit.UPDATE")
                        : t("admin.sub_major_edit.ADD")
                }
                items={[
                    {
                        href: RoutesString.ADMIN_SUB_MAJOR_LIST,
                        title: t("admin.sub_major_list.LIST"),
                    },
                ]}
            />

            <PageHeader
                headerTitle={
                    isUpdatePage
                        ? t("admin.sub_major_edit.UPDATE")
                        : t("admin.sub_major_edit.ADD")
                }
            />

            <div className="page-content">
                <Form>
                    <Notification
                        color={notification.color}
                        isOpen={notification.isOpen}
                        messages={notification.messages}
                    />
                    <FormGroup className="row">
                        <Label for="value" className="col-lg-3">
                            {t("admin.sub_major_list.COURSE")} <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaUserAlt />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="number"
                                    name="khoa"
                                    placeholder={t(
                                        "admin.sub_major_edit.COURSE_PLACEHOLDER"
                                    )}
                                    value={values.khoa}
                                    onChange={handleChange}
                                    invalid={errors.khoa}
                                />
                            </InputGroup>
                            {errors.khoa && (
                                <FormText color="danger">
                                    {errors.khoa}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup className="row">
                        <Label for="name" className="col-lg-3">
                            {t("admin.sub_major_list.CAREER_OPPORTUNITIES")}{" "}
                            <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaTextWidth />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="text"
                                    name="coHoiNgheNghiep"
                                    placeholder={t(
                                        "admin.sub_major_edit.CAREER_OPPORTUNITIES_PLACEHOLDER"
                                    )}
                                    value={values.coHoiNgheNghiep}
                                    onChange={handleChange}
                                    invalid={errors.coHoiNgheNghiep}
                                />
                            </InputGroup>
                            {errors.coHoiNgheNghiep && (
                                <FormText color="danger">
                                    {errors.coHoiNgheNghiep}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup className="row">
                        <Label for="name" className="col-lg-3">
                            {t("admin.sub_major_list.COMMON_TARGET")}{" "}
                            <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaTextWidth />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="text"
                                    name="mucTieuChung"
                                    placeholder={t(
                                        "admin.sub_major_edit.COMMON_TARGET_PLACEHOLDER"
                                    )}
                                    value={values.mucTieuChung}
                                    onChange={handleChange}
                                    invalid={errors.mucTieuChung}
                                />
                            </InputGroup>
                            {errors.mucTieuChung && (
                                <FormText color="danger">
                                    {errors.mucTieuChung}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup className="row">
                        <Label for="name" className="col-lg-3">
                            {t("admin.sub_major_list.MAJOR")} <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaTextWidth />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="select"
                                    name="nganhDaoTao"
                                    value={values.nganhDaoTao}
                                    onChange={handleChange}
                                >
                                    <option key={1} value={-1}>
                                        {t("admin.sub_major_edit.CHOOSE_MAJOR")}
                                    </option>
                                    {listMajor.length > 0 &&
                                        listMajor.map((item, index) => {
                                            return (
                                                <option
                                                    key={index + 1}
                                                    value={item.id}
                                                >
                                                    {/* {t(
                                                        "admin.sub_major_list.MAJORS_NAME"
                                                    )} :  */}
                                                    {`${item.ten} - ${item?.chuongTrinhDaoTao?.maCTDT}`}
                                                </option>
                                            );
                                        })}
                                </Input>
                            </InputGroup>
                            {errors.nganhDaoTao && (
                                <FormText color="danger">
                                    {errors.nganhDaoTao}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup>
                        <Button color="success" onClick={handleEditOnClick} disabled={isErrors(errors)}>
                            {isUpdatePage ? (
                                <>
                                    <FaExchangeAlt /> {t("admin.common.UPDATE")}
                                </>
                            ) : (
                                <>
                                    <FaPlus /> {t("admin.common.ADD")}
                                </>
                            )}
                        </Button>
                        &emsp;
                        {isUpdatePage && (
                            <>
                                <Button onClick={handleRefreshOnClick}>
                                    <FaSync /> {t("admin.common.REFRESH")}
                                </Button>
                                &emsp;
                                <Button
                                    color="danger"
                                    onClick={handleDeleteOnClick}
                                >
                                    <FaTrash /> {t("admin.common.DELETE")}
                                </Button>
                            </>
                        )}
                    </FormGroup>
                </Form>
            </div>
        </div>
    );
};

const SubMajorEditFormikForm = withFormik({
    mapPropsToValues(props) {
        return {
            khoa: new Date().getFullYear(),
            coHoiNgheNghiep: "",
            mucTieuChung: "",
            nganhDaoTao: 0,
        };
    },
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
        khoa: Yup.number()
            .required(i18n.t("REQUIRED_FIELD_WARNING"))
            .positive(i18n.t("admin.POSITIVE_WARNING")),
        nganhDaoTao: Yup.number()
            .required(i18n.t("REQUIRED_FIELD_WARNING"))
            .positive(i18n.t("REQUIRED_FIELD_WARNING")),
        mucTieuChung: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
        coHoiNgheNghiep: Yup.string().required(
            i18n.t("REQUIRED_FIELD_WARNING")
        ),
    }),
})(SubMajorEdit);

export default SubMajorEditFormikForm;
